
table.freetimeTable {
    --noTime-fill: repeating-linear-gradient(45deg,
        rgba(0, 0, 0, 0.2),
        rgba(0, 0, 0, 0.2) 9%,
        rgba(0, 0, 0, 0.3) 9%,
        rgba(0, 0, 0, 0.3) 18%);

    width: fit-content
}

.freetimeTable th,
.freetimeTable td {
    border-style: none solid;
    border-color: #ffffff3b;
    border-width: 1px;
}


.freetimeTable td {
    min-width: 2.7em;
    height: 1.5em;
    padding: 2px 2px;

    color: #00000047;
    /* color: #ffffff00; */
    font-weight: bold;
}

.freetimeTable td:hover {
    /* border: 3px solid;
    box-sizing: border-box; */
    background-color: rgb(87, 128, 251);
    color: rgb(238, 238, 238)
}

.freetimeTable th {
    position: -webkit-sticky;
    position: sticky;
    left: 0px;
    border-right: solid white 1px;
    padding: 2px 2px;
    font-weight: bold
}

.freetimeTable .th0 {
    width: 4em;
    background-color: #D8E6F3;
    z-index: 10;
    top: 0;
    left: 0;
}




.freetimeTable .td-header {
    width: 3em;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;

    height: 2.5vh;
    border-bottom: solid black 1px;
    background-color: #ffffff;
    color: Navy;
}




.freetimeTable .groupN {
    background-color: #6daae6;
    background-color: #bfddf9;
}

.freetimeTable .groupN.odd {
    background-color: #83bbdc;
    background-color: #cfedff;
}

.freetimeTable .groupD {
    background-color: #29d214;
    background-color: #a8ff9e;
}

.freetimeTable .groupD.odd {
    background-color: #63d666;
    background-color: #c9ffcb;
}




.freetimeTable .HDay {
    background-color: #df4740 !important;
    color: #ffffff !important;
}

.freetimeTable .tdHDay {
    background-color: #ff837dcc;
}

.freetimeTable .trPrimeTime,
.freetimeTable .trPrimeTime td {
    border-bottom: solid #ff5757 1px;
}



.freetimeTable .fullTime {
    color: #ffffff00 !important;
}

.freetimeTable .noTime {
    background-image: var(--noTime-fill);
    color: #00000000 !important;
}

.freetimeTable .lessTime {
    background-image: var(--noTime-fill);
    color: #ffffff98 !important;
}

.freetimeTable .isPast {
    background-color: #e7e7e7;
}