
.bookingsTable {
  font-size: 2vh;
  vertical-align: top;
}



.bookingsTable td {
  max-width: 18em;
  padding: 1px 2px 3px 2px;
  height: unset;
  color: #000000a8;
  font-weight: normal;

  text-align: left;
}
.bookingsTable td:nth-of-type(1) {
  text-align: right;
  min-width: 1em;
}


.bookingsTable th {
  position: -webkit-sticky;
  position: sticky;
  left: 0px;

  border-right: solid white 1px;
  padding: 2px 2px;
  font-weight: bold
}

.bookingsTable pre {
    margin: 0;
    white-space: pre-wrap;
    font-size: 1em;
}


#btHeaderTr {
    height: 2.1em;
    border: none;
}

.bookingsTable .th-header {
  top: 0px;
  height: 4vh;
  border-bottom: solid black 2px;
  background-color: #ffffff;
  color: rgb(0 90 255);

  padding: 0;
  vertical-align: middle;
  font-size: clamp(14px, 1.75vh, 1.5rem);
  text-transform: uppercase;
  text-align: center !important;
}

.bookingsTable .th-header.th0 {
    z-index: 10;
    top: 0; left: 0;
  }

.bookingsTable .th-header:nth-of-type(4),
.bookingsTable .th-header:nth-of-type(5) {
  min-width: 10em;
}



.curDayDiv {
  position: absolute;
  top  : 5%;
  left : 0.5%;
  width: 99%;

  z-index: 20;

  padding: 0.3em 0;
  border-radius: 2px;
  box-shadow: 0px 4px 5px 1px rgba(0,0,0,0.7);

  text-align : center;
  font-size  : 2vh;
  font-weight: bold;

  vertical-align: middle;
  background-color: #e0dede
}
.curDayDiv.HDay {
    background-color: #ffa1a1;
}


.newDayTr th,
.newDayTr td {
  height: 3vh;
  padding: 0.2em;
  border-top: 2px solid black;

  font-weight: bold;
  color: #464646;
  background-color: #ffffff;
}
.newDayTr th {
  z-index: 1;
}
.newDayTr.HDay th,
.newDayTr.HDay td {
  color: crimson;
}



.tdSpan {
  display: inline-block;
  color: #0000ff5c;
}

td.freeTimeTd {
  text-align: left !important;
  vertical-align: middle;
  padding: 0 0 0 0px;
  font-style: italic;
  color: #252dbf;
}


.noTime-book th,
.noTime-book td {
  background-color: #56565680;
}


.pastSlot th,
.pastSlot td {
  color: #0000006e;
  font-style: italic;
  background-color: #ffffffb5;
}


.instrReq :is(th, td, button) {
    background-color: red !important;
    color: white;
}



.bookingsTable  .groupN {
  background-color: #bfddf9;
}
.bookingsTable .groupN.odd {
  background-color: #cfedff;
}
.bookingsTable .groupD {
  background-color: #a8ff9e;
}
.bookingsTable .groupD.odd {
  background-color: #c9ffcb;
}

.bookingsTable .pastSlot {
    background-color: #f2f2f2;
}
.bookingsTable .pastSlot.odd {
    background-color: #ffffff;
}


.bookingsTable tr:hover,
.bookingsTable tr.odd:hover {
    background-color : rgb(118 176 226);
}


.bt-serv-tbody {
    height: 100vh;
    text-align: center;
}

.bt-serv-tbody
td.bt-serv-td {
    color: #a00202a8;
    background-color: #fabcbca8;
    font-weight: normal;

    text-align: center;
    vertical-align: top;
}


.freeTimeButton {
    width: 13em;
    text-align: left;
}


.freeTimeButton,
.editRecButton {
    border: none;
    box-shadow: 3px 2px 5px 1px #5b5b5b;
    height: 3vh;

    font-size: 1.7vh;
    color: inherit;
    background-color: #d7faff;
}

.groupD .freeTimeButton,
.groupD .editRecButton {
    background-color: #caffc3;
    outline-color : rgb(102, 237, 145);
}

.pastSlot .freeTimeButton,
.pastSlot .editRecButton {
    background-color: #f3f3f3;
}



.blinkElem {
  background-color: #a8ff9e !important;
  animation: change-background 1s linear infinite;
}
  @keyframes change-background {
    25%, 75% { background: #a8ff9e; }
    50% { background: #0600ff; }
  }





