#sidebar {
    --BASE: 16px;
    position: relative;
    /* display: inline-flex; */
    /* overflow-x: scroll; */
    /* overflow-y: scroll; */
    /* -webkit-overflow-scrolling: touch; */
    font-size: var(--BASE);


    min-width : 20em;
    max-width : 20em;
    height    : 84vh;
    margin    : max(0.5vw, 0.5em);
    /* padding   : 0 3px; */

    overflow:  hidden;

    height: 84vh;

    border       : none;
    border-radius: 5px;
    box-shadow   : 3px 3px 9px 3px #000000e3;

    background-color: rgb(255 255 255);
}

.sidebar-header {
    position      : relative;
    width         : 100%;
    margin        : 0;
    padding       : 0.5em 0;
    /* border-radius : 10px; */

    text-align    : center;
    font-weight   : bold;
    font-size: clamp(16px, 2.5vh, 24px);

    color            : #f0f0f0;
    background-color : #213ebb;
}


#bookingEditorModal {
    position : absolute;
    top      : 0;
    left     : 0;
    z-index  : 100;
    width    : 100%;
    height   : 100%;

    box-sizing : border-box;
    padding    : 30% 0;

    white-space: pre-line;
    text-align : center;


    background-color: #80a7d2cf;
    border-radius   : 10px;


    font-size  : 2em;
    font-weight: 700;
    color      : #ffffff;
}






  .side-form {
    position    : relative;
    width       : 99%;
    box-sizing  : border-box;
    margin      : 0.5em auto;
    padding     : 20px;

    border-radius: 10px;
    background   : #f0f0f0;

    --input_right-width: 6.5rem;
  }

  .side-form form {
    text-align: left;
  }

  .side-form__inlineWrapper{
    display        : flex;
    justify-content: space-between;
  }

  .side-form label {
    display   : block;
    text-align: center;
    color: #686868;
  }

  #fullnameLabel {
      margin-top: clamp(0.5rem, 1rem, 1.55vh);
  }

  .side-form :is(input, select) {
    display    : block;
    box-sizing : border-box;
    width  : 100%;
    margin : 0 0 10px;
    padding: 5px;

    border        : 1px solid #c0c0c0;
    border-radius : 5px;

    text-align  : center;
    line-height : 20px;
    font-size   : 16px;
    font-weight : bold;

    color : royalblue
  }

  .side-form :is(input, select)::placeholder {
    font-weight : 400;
    color : rgb(224, 225, 228);
  }

  .side-form :is(input, select):disabled {
      color: rgb(175 175 175);
      background-color: inherit;
  }




  .side-form input[name="bookingId"] {
    display      : inline;
    width        : var(--input_right-width);
    margin-left  : -2em;
    padding-right: 0;
    /* line-height: 1.5em; */

    text-align: right;
    color: gray
  }

  .side-form input[name="bookingId"]::placeholder {
    color: gray;
    padding-right: 1em;
  }

  #bookingIdDiv {
    display: flex;
    justify-content: right;
    line-height: 2em;
  }
  #bookingId-label {
      display    : inline;
      font-weight: bold;
  }



  .side-form input[name="Flight_Date"] {
    display: inline;
    width: 10.5rem;
    font-size: clamp(1em, 1.5em, 3vh);
  }
  .side-form input[type="date"]::-webkit-calendar-picker-indicator {
    margin-right: -0.25em;
    margin-left: 0;
  }


  .side-form select[name="TimeSlot"] {
    display     : inline;
    width       : var(--input_right-width);
    margin-left : auto;
    text-align  : right;

    font-size: clamp(1em, 1.5em, 3vh);
  }




  .side-form input[name="timeVal"] {
    width       : 5rem;
    margin      : 0;
    padding     : 0.05em;

    border-radius: 0;
    border: 1px solid rgb(104, 117, 255);

    text-align: center;

    font-size  : 2rem;
    font-weight: bold;
  }
  .side-form input[name="timeVal"]::-webkit-outer-spin-button,
  .side-form input[name="timeVal"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }



  .side-form select[name="status"] {
    width       : var(--input_right-width);
    margin-left : auto;
  }




  .side-form button {
    background: #0d718d;
    color: #fff;
    padding: 5px 20px;
    border: 1px solid #064758;
    border-radius: 5px;
    font-size: 16px;
    line-height: 20px;
    cursor: pointer;
  }


  .side-form .stepInputButton {
    width : 100%;
    height: 1.5em;
    padding: 0;

    border        : none;
    border-radius : 10px 10px 0 0;

    background: rgb(104, 117, 255);
    color     : #ffffff;

    font-weight: bold;
  }
  .side-form .stepInputButton.stepInputButton--bottom {
    border-radius : 0 0 10px 10px;
  }

  .side-form .quickInputBlock {
      display: block;
      padding-top: 1em;
      margin: 0 3em 0 -1px;
  }
  .side-form .quickInputBlock-button {
    display    : block;
    width      : 2em;
    height     : 1em;
    margin     : 7px 0px 0 0px;
    padding    : 7px 0px;
    line-height: 0;

    border: none;
    font-size: 1em;

    background: inherit;
    color     :  rgb(104, 117, 255);
  }
  .side-form .quickInputBlock-button:hover {
    background: #bbbbff;
  }

  #SubmitBlock {
    text-align: center;
  }
  #SubmitBlock>button {
    margin-top: clamp(1rem, 2rem, 3vh);
    width : 100%;
    height: 2.5em;
    border: none;

    background-color: #00a18a;
    font-weight: bold;
  }
  #SubmitBlock>button:disabled {
    background-color: #7dafa8;
    color           : #cdcdcd
  }
  #SubmitBlock>button[name = 'toCancel'] {
    background-color: rgb(249 26 26);
  }




  .side-form .error {
      color: red;
      margin: -5px 0 10px;
  }

#closeSidebarBtn {
    position : absolute;
    top      : 10%;
    right    : 1.5%;
    height   : 80%;
    width    : 10%;

    border-style  : solid;
    border-radius : 6px;
    border-color  : #ffffff;

    font-weight: 700;

    color            : #ffffff;
    background-color : inherit;
}




