body {
    font-family: sans-serif;
  }

  input {
    border: 1px solid #999;
    padding: 0.5rem;
    width: 300px;
  }

  .no-suggestions {
    color: #999;
    padding: 0.5rem;
  }

  .suggestions {
    position: absolute;
    width: 95%;
    min-height: 8em;
    max-height: 20em;
    margin-top: 0;
    padding-left: 0;
    border  : 1px solid #2343ba;
    border-radius: 5px;
    list-style: none;
    overflow-y: auto;
    background-color: white;
  }

  .suggestions li {
    padding: 0.5rem;
  }

  .suggestion-active,
  .suggestions li:hover {
    background-color: #8ec5f2;
    color: #03136a;
    cursor: pointer;
    font-weight: 700;
  }

  .suggestions li:not(:last-of-type) {
    border-bottom: 1px solid #2343ba;
  }

  .suggestions li:first-of-type {
      color: royalblue;
      font-size   : larger;
      font-weight : bold;
      font-style  : italic;
  }
