@media all and (orientation:landscape) {

    button {
        font-size: 16px;
        height: 1.7em;
    }

    .inner {
        height: 85vh;
    }

    table,
    .bookingsTable,
    #btHeaderTr .td-header {
        font-size: 12px;
    }
}

@media all and (min-width: 900px) and (orientation: landscape) {
    /* body {
        background-color: rgb(202, 255, 195);
    } */

    table,
    .bookingsTable,
    #btHeaderTr .td-header {
        font-size: 14px;
    }

    table {
        --noTime-fill: repeating-linear-gradient(
            45deg,
            rgba(0, 0, 0, 0.2),
            rgba(0, 0, 0, 0.2) 9%,
            rgba(0, 0, 0, 0.3) 9%,
            rgba(0, 0, 0, 0.3) 18%
          );
    }
}