.flyersForm {
    position: relative;
    background: #f0f0f0;
    border-radius: 10px;
    padding: 20px;
    margin: 20px auto;
    width: 100%;
    max-width: 17rem;
  }

  .flyersForm form {
    text-align: left;
  }

  .flyersForm label {
    display: block;
    color: #686868;
  }

  .flyersForm input,
  .flyersForm select {
    display    : block;
    box-sizing : border-box;
    width  : 100%;
    margin : 0 0 10px;
    padding: 5px;

    border        : 1px solid #c0c0c0;
    border-radius : 5px;

    text-align  : center;
    line-height : 20px;
    font-size   : 16px;
    font-weight : bold;

    color : royalblue
  }


  .flyersForm select[name="status"] {
    margin-left: auto;
    width: 10em;
  }




  .flyersForm__inlineWrapper{
    display: flex;
  }

  .flyersForm button {
    width      : 100%;
    height     : 2.5em;
    padding    : 5px 20px;
    line-height: 20px;

    border       : none;
    border-radius: 5px;

    font-size  : 16px;
    font-weight: bold;

    cursor: pointer;

    background : #0d718d;
    color      : #fff;
  }

  #SubmitBlock>button {
    margin-top: 1.5em;
    width : 100%;
    height: 2.5em;
    border: none;

    background-color: #00a18a;
    font-weight: bold;
  }

  .flyersForm button:disabled {
    background-color: #7dafa8;
    color           : #cdcdcd
  }


  .flyersForm .error {
      color: red;
      margin: -5px 0 10px;
    }




#flyersContainer {
    position: absolute;
    top     : 0;
    left    : 0;
    z-index: 100;
    width : 100%;
    height: 100%;
    background-color: #0330faa9;
    border-radius: 10px;
}

#closeFEDBtn {
    position: absolute;
    top: 10%;
    right: 2%;
    background-color: inherit;

    border-style: solid;
    border-radius: 6px;
    border-color: #ffffff;

    height: 80%;
    width: 10%;

    line-height: 0;
    font-weight: 700;
    color: #ffffff;
}

#flyerEditorModal {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 30% 0;

    white-space: pre-line;
    text-align : center;


    background-color: #80a7d2cf;
    border-radius: 10px;


    font-size: 2em;
    font-weight: 700;
    color: #ffffff;
}



.flyersForm-header {
    position: relative;
    margin-top: 0.5em;
    margin-bottom: -0.5em;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    text-align: center;
    width: 14rem;
    padding: 0.5em 2.5em;
    background-color: #0332fa;
    color:#f0f0f0;
    font-weight: bold;
    font-size: clamp(16px, 2.5vh, 24px);
}