  .login-form {
    position: relative;
    display        : flex;
    justify-content: center;
    width: 100%;
    background: #15aec0;
  }

  .login-form form {
    margin    : 15% 1em;
    max-width : 20em;
    min-width : 15em;
    height    : max-content;
    padding   : 1em;

    text-align: left;
    border-radius: 10px;
    background: #f0f0f0;
  }

  .login-form label {
    display: block;
  }

  .login-form input {
    display: block;
    box-sizing: border-box;
    margin: 5px 0 10px;
    padding: 5px;
    border: 1px solid #c0c0c0;
    border-radius: 5px;
    font-size: 16px;
    line-height: 20px;
    width: 100%;
  }

  .login-form button {
    background: #0d718d;
    color: #fff;
    padding: 5px 20px;
    border: 1px solid #064758;
    border-radius: 5px;
    font-size: 16px;
    line-height: 20px;
    cursor: pointer;
  }


  .login-form .error {
      color: red;
      margin: -5px 0 10px;
    }


#loginForm-btn-block {
    display: flex;
    padding: 0;
    text-align: right;
}

#restorePass {
    margin-left: auto;
}
#restorePass:disabled {
    background-color: #c0c0c0;
    color:#6a95a1
}

#loginInfo {
    position: absolute;
    box-sizing: border-box;
    padding: 20vh 0.25em;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    background-color: #e2f3ffb3;
    text-align: center;
    font-weight: bold;
    font-size:   2em;
}


.loginInfoBtn {
    height: 3em;
    width: 100%;
}

.loginInfoBtn.error {
    background-color: rgb(187, 78, 78);
    color: lightgray;
}

.loginInfoBtn.hidden {
    display: none
}